import { createBrowserRouter, RouterProvider } from 'react-router-dom'
import { RoutePaths } from './enums/routeEnum'
import Root from './pages/Root'
import ErrorPage from './pages/ErrorPage'
import Login from './pages/Login'
import ScriptListPage from './pages/Script/ListPage'
import EditScriptPage from './pages/Script/EditScriptPage'
import NewScriptPage from './pages/Script/NewScriptPage'
import SubmittedListPage from './pages/Script/SubmittedListPage'
import ScriptPage from './pages/Script/ScriptPage'
import ProfilePage from './pages/ProfilePage'
import AboutPage from './pages/AboutPage'
import ActivitiesPage from './pages/ActivitesPage'
import HelpPage from './pages/HelpPage'

const router = createBrowserRouter([
  {
    path: '/',
    element: <Root />,
    errorElement: <ErrorPage />,
    children: [
      { index: true, element: <ScriptListPage /> },
      {
        path: RoutePaths.SCRIPT_LIST,
        element: <ScriptListPage />,
      },
      {
        path: RoutePaths.SCRIPT_DETAIL,
        element: <ScriptPage />,
      },
      {
        path: RoutePaths.SCRIPT_EDIT,
        element: <EditScriptPage />,
      },
      {
        path: RoutePaths.SCRIPT_NEW,
        element: <NewScriptPage />,
      },
      {
        path: RoutePaths.SCRIPT_SUBMITTED,
        element: <SubmittedListPage />,
      },
      {
        path: RoutePaths.PROFILE,
        element: <ProfilePage />,
      },
      {
        path: RoutePaths.ABOUT,
        element: <AboutPage />,
      },
      {
        path: RoutePaths.ACTIVITIES,
        element: <ActivitiesPage />,
      },
      {
        path: RoutePaths.HELP,
        element: <HelpPage />,
      },
    ],
  },
  {
    path: RoutePaths.LOGIN,
    element: <Login />,
  },
])

const App = () => <RouterProvider router={router} />

export default App
