import { motion, Transition } from 'framer-motion'

interface ListRowProps {
  children: React.ReactNode
  contentTransition?: Transition
  lineTransition?: Transition
}

const ListRow = ({
  children,
  contentTransition,
  lineTransition,
}: ListRowProps) => (
  <li className="flex flex-col hover:bg-black hover:text-white">
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 100 }}
      transition={contentTransition}
    >
      {children}
    </motion.div>
    <motion.figure
      className="block h-px w-full origin-left bg-black"
      initial={{ scaleX: 0 }}
      animate={{ scaleX: 1 }}
      transition={lineTransition}
    />
  </li>
)

export default ListRow

ListRow.defaultProps = {
  contentTransition: { duration: 1.25, ease: 'easeOut' },
  lineTransition: { duration: 1.25, ease: 'easeInOut' },
}
