import { useContext, useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { format } from 'date-fns'
import { motion } from 'framer-motion'
import AppContext from '../../context/AppContext'
import { Script } from '../../typings'
import Accordion from '../../components/Accordion/Accordion'

const parseYouTubeURL = (url: string): string => {
  const regExp =
    /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/
  const match = url.match(regExp)
  return match && match[7].length === 11 ? match[7] : ''
}

interface YoutubeVideoProps {
  url: string
}

const YoutubeVideo = ({ url }: YoutubeVideoProps) => (
  <div className="relative aspect-video w-full">
    <iframe
      className="absolute top-0 left-0 h-full w-full"
      src={`https://www.youtube.com/embed/${parseYouTubeURL(url)}`}
      allowFullScreen
    />
  </div>
)

const ScriptPage = () => {
  const { scriptId } = useParams()
  const { fetchScript } = useContext(AppContext)
  const [script, setScript] = useState<null | Script>(null)
  const navigate = useNavigate()

  const [isAdvloglineOpen, setIsAdvloglineOpen] = useState<boolean>(true)
  const [isBasicVideoOpen, setIsBasicVideoOpen] = useState<boolean>(false)
  const [isAdvVideoOpen, setIsAdvVideoOpen] = useState<boolean>(false)
  const [isFullVideoOpen, setIsFullVideoOpen] = useState<boolean>(false)

  useEffect(() => {
    fetchScript(scriptId ?? '').then((v) => {
      setScript(v)
    })
  }, [fetchScript, setScript, scriptId])

  if (script === null) {
    return <>Loading...</>
  }

  return (
    <main className="grid h-screen grid-cols-[1fr_1px_1fr] overflow-y-hidden">
      <section className="max-h-screen overflow-y-auto py-5 px-6">
        <motion.button
          type="button"
          onClick={() => navigate(-1)}
          onKeyDown={() => navigate(-1)}
          className="cursor-pointer"
          initial={{ opacity: 0, y: '50%' }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1, duration: 1.25, ease: 'easeOut' }}
        >
          返回
        </motion.button>
        <motion.h1
          className="mt-4 text-5xl"
          initial={{ opacity: 0, y: '50%' }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1, duration: 1.25, ease: 'easeOut' }}
        >
          {script.name}
        </motion.h1>
        <motion.div
          className="mt-10"
          initial={{ opacity: 0, y: '10%' }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1.25, duration: 1.25, ease: 'easeOut' }}
        >
          {script.basic_logline ||
            '理需要知，等等等等咁啦。簡單講講以下呢三場 戲做緊乜鳩，有乜前文後理需要知，等等等等咁 啦。簡單講講以下呢三場戲做緊乜鳩，有乜前文 後理需要知，等等等等咁啦。'}
        </motion.div>
        <motion.div
          className="mt-10 grid grid-cols-[repeat(auto-fill,_minmax(160px,_1fr))] gap-6"
          initial={{ opacity: 0, y: '50%' }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1.25, duration: 1.25, ease: 'easeOut' }}
        >
          <div>
            <div className="text-slate-500">類型</div>
            <div>{script.hashtags.map((tag) => `${tag}`).join(', ')}</div>
          </div>
          <div>
            <div className="text-slate-500">編劇</div>
            <div>
              {script.usernames.map((username) => `${username}`).join(', ')}
            </div>
          </div>
          <div>
            <div className="text-slate-500">上傳日期</div>
            <div>{format(Number(script.created_at) * 1000, 'yyyy-MM-dd')}</div>
          </div>
        </motion.div>
      </section>
      <motion.figure
        className="block h-screen w-px origin-top bg-black"
        initial={{ scaleY: 0 }}
        animate={{ scaleY: 1 }}
        transition={{ duration: 1.25, ease: 'easeInOut' }}
      />
      <section className="max-h-screen overflow-y-auto">
        <ul id="accordion-list">
          <Accordion
            title="1500字大綱"
            content={script.adv_logline}
            isOpen={isAdvloglineOpen}
            onTitleClick={() => setIsAdvloglineOpen(!isAdvloglineOpen)}
          />
          <Accordion
            title="30秒 LOGLINE video"
            content={<YoutubeVideo url={script.basic_url} />}
            isOpen={isBasicVideoOpen}
            onTitleClick={() => setIsBasicVideoOpen(!isBasicVideoOpen)}
          />
          <Accordion
            title="90秒故事簡介 video"
            content={<YoutubeVideo url={script.adv_url} />}
            isOpen={isAdvVideoOpen}
            onTitleClick={() => setIsAdvVideoOpen(!isAdvVideoOpen)}
          />
          <Accordion
            title="180秒完整大綱 video"
            content={<YoutubeVideo url={script.full_url} />}
            isOpen={isFullVideoOpen}
            onTitleClick={() => setIsFullVideoOpen(!isFullVideoOpen)}
          />
        </ul>
      </section>
    </main>
  )
}

export default ScriptPage
