import { useContext, useEffect, useState } from 'react'
import { Script } from '../../typings'
import ScriptForm from '../../components/form/Script'
import AuthContext from '../../context/AuthContext'

const NewScriptPage = () => {
  const { authUser } = useContext(AuthContext)
  const [script, setScript] = useState<null | Script>(null)

  useEffect(() => {
    setScript({
      usernames: [authUser?.username].filter((v) => v) as string[],
      hashtags: [],
      name: '',
      basic_logline: '',
      adv_logline: '',
      logline_file_id: '',
      basic_url: '',
      adv_url: '',
      full_url: '',
      is_deleted: 0,
    })
  }, [authUser?.username])

  if (script === null) {
    return <>Loading</>
  }

  return <ScriptForm script={script} />
}

export default NewScriptPage
