import React, { useContext, useCallback, useMemo } from 'react'
import AuthContext from './AuthContext'
import { FileType, Script, ScriptEntry } from '../typings'

export interface AppContextProps {
  fetchScriptList: (
    page: number,
    pageSize: number,
    usename?: string
  ) => Promise<{ data: ScriptEntry[]; totalCount: number }>
  fetchScript: (id: string) => Promise<Script>
  saveScript: (script: Script) => Promise<string> // return script ID
  uploadScriptFile: (data: FormData) => Promise<FileType>
  fetchYearScripts: (
    year: number
  ) => Promise<{ data: ScriptEntry[]; totalCount: number }>
  getVotes: () => Promise<{ scriptIds: string[] }> // return vote dictionary of the logged in user
  upVote: (id: string) => Promise<'success' | 'failed'>
  downVote: (id: string) => Promise<'success' | 'failed'>
}

const AppContext = React.createContext({} as AppContextProps)

export const AppContextProvider = ({
  children,
}: {
  children: React.ReactNode
}) => {
  const { authFetch } = useContext(AuthContext)

  const fetchScriptList = useCallback(
    (page: number, pageSize: number, username?: string) =>
      authFetch(
        `/script/list?${new URLSearchParams({
          page: `${page}`,
          pageSize: `${pageSize}`,
          username: username ?? '',
        })}`
      ).then((r) => r.json()),
    [authFetch]
  )

  const fetchScript = useCallback(
    (id: string) =>
      authFetch(`/script/read?${new URLSearchParams({ id })}`).then((r) =>
        r.json()
      ),
    [authFetch]
  )

  const saveScript = useCallback(
    (script: Script) =>
      authFetch('/script/upsert', {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(script),
      }).then((r) => r.json()),
    [authFetch]
  )

  const uploadScriptFile = useCallback(
    (data: FormData) =>
      authFetch('/file/upload', {
        method: 'POST',
        body: data,
      }).then((r) => r.json()),
    [authFetch]
  )

  const upVote = useCallback(
    (id: string) =>
      authFetch('/script/up-vote', {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(id),
      }).then((r) => r.json()),
    [authFetch]
  )

  const downVote = useCallback(
    (id: string) =>
      authFetch('/script/down-vote', {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(id),
      }).then((r) => r.json()),
    [authFetch]
  )

  const fetchYearScripts = useCallback(
    (year: number) =>
      authFetch(
        `/script/list-year?${new URLSearchParams({
          year: `${year}`,
        })}`
      ).then((r) => r.json()),
    [authFetch]
  )

  const getVotes = useCallback(
    () => authFetch('/script/get-votes').then((r) => r.json()),
    [authFetch]
  )

  return useMemo(
    () => (
      <AppContext.Provider
        value={{
          fetchScriptList,
          fetchScript,
          saveScript,
          uploadScriptFile,
          fetchYearScripts,
          upVote,
          downVote,
          getVotes,
        }}
      >
        {children}
      </AppContext.Provider>
    ),
    []
  )
}

export default AppContext
