import { AnimatePresence, motion } from 'framer-motion'
import Plus from '../../plus.svg'
import Minus from '../../minus.svg'

interface AccordionProps {
  title: string
  content: React.ReactNode
  isOpen: boolean
  onTitleClick: () => void
}

const Accordion = ({
  title,
  content,
  isOpen,
  onTitleClick,
}: AccordionProps) => (
  <motion.li
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    transition={{ delay: 1, duration: 1.25, ease: 'easeOut' }}
  >
    <section
      className="flex cursor-pointer place-content-between border-b border-black bg-white px-6 py-10 text-2xl"
      onClick={onTitleClick}
    >
      <h2>{title}</h2>
      <figure className="flex place-content-between">
        {isOpen ? (
          <img src={Minus} alt="關閉" />
        ) : (
          <img src={Plus} alt="展開" />
        )}
      </figure>
    </section>
    <AnimatePresence initial={false}>
      {isOpen && (
        <motion.div
          initial="collapsed"
          animate="open"
          exit="collapsed"
          variants={{
            open: { opacity: 1, height: 'auto' },
            collapsed: { opacity: 0, height: 0 },
          }}
          transition={{ duration: 1, ease: 'easeOut' }}
        >
          <div className="border-b border-black bg-white px-6 py-10">
            {content}
          </div>
        </motion.div>
      )}
    </AnimatePresence>
  </motion.li>
)

export default Accordion
