import { useLocation, useParams } from 'react-router-dom'

export default () => {
  const location = useLocation()
  const params = useParams()
  const basePath = Object.entries(params).reduce(
    (path, keyValue) =>
      path.replace(keyValue[1]!.toString(), `:${keyValue[0]}`),
    location.pathname
  )
  return basePath
}
