import { useContext, useState } from 'react'
import { Outlet, Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import Hamburger from '../hamburger.svg'
import Close from '../close.svg'
import AuthContext from '../context/AuthContext'
import ModalContext from '../context/ModalContext'
import Modal from '../components/Modal/Modal'
import useBasePath from '../hooks/useBasePath'
import { RouteNames, RoutePaths } from '../enums/routeEnum'
import LoginModalContent from '../components/Modal/LoginModalContent'
import Header from '../components/Header/Header'

interface MenuProps {
  isOpen: boolean
  handleClick: () => void
}

const Menu = ({ isOpen, handleClick }: MenuProps) => {
  const { authUser } = useContext(AuthContext)
  const { handleModal } = useContext(ModalContext)

  return (
    <nav
      className={`fixed z-10 w-[calc(100vw_-_60px)] bg-white	transition-all duration-300 ${
        isOpen ? 'z-2 opacity-1 h-screen' : 'z-[-1] h-0 opacity-0'
      }`}
    >
      <ul className="grid h-screen w-full auto-cols-max grid-flow-row">
        <li className="w-[calc(100vw_-_60px)] cursor-pointer">
          <Link
            onClick={handleClick}
            className="flex h-full w-full items-center justify-center text-xl"
            to="/"
          >
            主頁
          </Link>
        </li>
        <li className="w-[calc(100vw_-_60px)] cursor-pointer border border-t-black">
          {authUser ? (
            <Link
              onClick={handleClick}
              className="flex h-full w-full items-center justify-center text-xl"
              to="/scripts/new"
            >
              投稿
            </Link>
          ) : (
            <span
              onClick={() => handleModal(<LoginModalContent />)}
              className="flex h-full w-full items-center justify-center text-xl"
            >
              投稿
            </span>
          )}
        </li>
        <li className="w-[calc(100vw_-_60px)] cursor-pointer border border-t-black">
          <Link
            onClick={handleClick}
            className="flex h-full w-full items-center justify-center text-xl"
            to="/help"
          >
            幫助
          </Link>
        </li>
      </ul>
    </nav>
  )
}

const Root = () => {
  const { authUser } = useContext(AuthContext)
  const { isOpen, handleModal } = useContext(ModalContext)
  const [isMenuOpen, setIsMenuOpen] = useState<boolean>(false)
  const basePath = useBasePath().replace(/^\//i, '')

  const routeEnumIndex = Object.values(RoutePaths).findIndex(
    (routeName) => routeName === basePath
  )

  return (
    <div className="grid grid-cols-[60px_1fr]">
      <div className="flex">
        <motion.div
          className="flex h-screen w-full flex-col items-center justify-between py-6"
          initial={{ opacity: 0 }}
          animate={{ opacity: 100 }}
          transition={{ duration: 1.25, ease: 'easeOut', delay: 0.5 }}
          onAnimationComplete={() => {
            sessionStorage.setItem('welcomeAnimation', 'loaded')
          }}
        >
          <button
            type="button"
            className="cursor-pointer"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
          >
            {isMenuOpen ? (
              <img src={Close} alt="關閉" />
            ) : (
              <img src={Hamburger} alt="目錄" />
            )}
          </button>
          <div className="tracking-widest [writing-mode:tb]">
            {Object.values(RouteNames)[routeEnumIndex]}
          </div>
          {authUser ? (
            <Link to="/profile">
              <div className="h-8 w-8 rounded-full	 bg-slate-800" />
            </Link>
          ) : (
            <div
              className="cursor-pointer underline"
              onClick={() => handleModal(<LoginModalContent />)}
            >
              登入
            </div>
          )}
        </motion.div>
        <motion.figure
          className="block h-screen w-px origin-top bg-black"
          initial={{ scaleY: 0 }}
          animate={{ scaleY: 1 }}
          transition={{ duration: 1.25, ease: 'easeInOut' }}
        />
      </div>
      <div
        id="scrollableDiv"
        className={`h-screen overflow-y-auto ${isOpen && 'blur-md'}`}
      >
        <Menu isOpen={isMenuOpen} handleClick={() => setIsMenuOpen(false)} />
        {![
          RoutePaths.SCRIPT_EDIT,
          RoutePaths.SCRIPT_NEW,
          RoutePaths.SCRIPT_DETAIL,
        ].includes(basePath as RoutePaths) && <Header />}
        <Outlet />
      </div>
      <Modal />
    </div>
  )
}

export default Root
