import React, { useContext } from 'react'
import { useNavigate } from 'react-router-dom'
import AuthContext from '../context/AuthContext'

const ProfilePage = () => {
  const { authUser, logoutFunc } = useContext(AuthContext)
  const navigate = useNavigate()
  return (
    <>
      <div>你好，{authUser?.username}！</div>
      <button
        type="submit"
        onClick={() => {
          logoutFunc()
          navigate('/')
        }}
        onKeyDown={() => {
          logoutFunc()
          navigate('/')
        }}
      >
        登出
      </button>
    </>
  )
}

export default ProfilePage
