import { useState, useRef, useContext, useEffect } from 'react'
import { NavLink, useNavigate } from 'react-router-dom'
import { useForm, Controller, SubmitHandler } from 'react-hook-form'
import AuthContext from '../../context/AuthContext'
import { Script } from '../../typings'
import AppContext from '../../context/AppContext'
import Logo from '../../logo.svg'

interface ScriptFormProps {
  script: Script
}

const DEFAULT_SCRIPT: Script = {
  usernames: [],
  hashtags: [],
  name: '',
  basic_logline: '',
  adv_logline: '',
  logline_file_id: '',
  basic_url: '',
  adv_url: '',
  full_url: '',
  is_deleted: 0,
}
const ScriptForm = ({ script }: ScriptFormProps) => {
  const [state, setState] = useState<Script>(script || DEFAULT_SCRIPT)
  const [submittedScriptId, setSubmittedScriptId] = useState<string | null>(
    null
  )
  const fileInput = useRef<HTMLInputElement>(null)
  const { authUser } = useContext(AuthContext)
  const { saveScript, uploadScriptFile } = useContext(AppContext)
  const navigate = useNavigate()
  const {
    control,
    formState: { errors },
    handleSubmit,
    setValue,
  } = useForm<Script>()

  useEffect(() => {
    if (submittedScriptId) {
      setTimeout(() => {
        navigate(`/scripts/${submittedScriptId}`)
      }, 1500)
    }
  }, [submittedScriptId])

  const handleChange = (field: keyof Script, value: any) => {
    setState((prev) => ({
      ...prev,
      [field]: value,
    }))
  }

  const handleUpload = () => {
    if (fileInput.current === null) return
    if (fileInput.current?.files?.length) {
      const form = new FormData()
      form.append('file', fileInput.current.files[0])
      form.append('name', fileInput.current.files[0].name)
      uploadScriptFile(form).then((file) => {
        handleChange('logline_file_id', file.id)
        setValue('logline_file_id', file.id)
      })
    }
  }

  const onSubmit: SubmitHandler<Script> = (data) => {
    const scriptData = {
      ...data,
      id: state.id || null,
      usernames:
        Array.isArray(state.usernames) && state.usernames.length
          ? state.usernames
          : [authUser!.username],
    }
    saveScript(scriptData).then((scriptId) => {
      if (scriptId) {
        setSubmittedScriptId(scriptId)
      }
    })
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <header className="sticky top-0 z-[1] flex h-[104px] flex-col justify-center">
        {submittedScriptId ? (
          <div className="flex h-full items-center justify-center bg-black text-lg text-white">
            已發布
          </div>
        ) : (
          <div className="flex h-full items-center justify-between border-b border-black bg-white pl-6 pr-8">
            <div className="flex gap-4">
              <h1>
                <NavLink to="/">
                  <img src={Logo} alt="三場戲" />
                </NavLink>
              </h1>
              <figure className="h-6 w-px bg-black" />
              <h2>草稿</h2>
            </div>
            <input
              className="cursor-pointer rounded-full border bg-black px-8 py-2 text-white"
              type="submit"
              value="儲存並發布"
            />
          </div>
        )}
      </header>

      <section className="mx-auto my-12 w-full max-w-xl">
        <div>
          <label className="block" htmlFor="name">
            名字
          </label>
          <Controller
            name="name"
            control={control}
            rules={{ required: true }}
            defaultValue={state.name}
            render={({ field }) => (
              <input
                className="w-full	border-b border-black py-2 focus:outline-none"
                {...field}
              />
            )}
          />
          {errors.name && '請輸入名字'}
        </div>

        <div className="mt-10">
          <label className="block" htmlFor="basic_logline">
            300字 LOGLINE
          </label>
          <Controller
            name="basic_logline"
            control={control}
            rules={{ required: true }}
            defaultValue={state.basic_logline}
            render={({ field }) => (
              <textarea
                rows={5}
                className="w-full	border-b border-black py-2 focus:outline-none"
                {...field}
              />
            )}
          />
          {errors.basic_logline && '請輸入300字 LOGLINE'}
        </div>

        <div className="mt-10">
          <label className="block" htmlFor="adv_logline">
            1500字大綱
          </label>
          <Controller
            name="adv_logline"
            control={control}
            rules={{ required: true }}
            defaultValue={state.adv_logline}
            render={({ field }) => (
              <textarea
                rows={25}
                className="w-full	border-b border-black py-2 focus:outline-none"
                {...field}
              />
            )}
          />
          {errors.adv_logline && '請輸入1500字大綱'}
        </div>

        <div className="mt-10">
          <label className="block" htmlFor="basic_url">
            30秒 LOGLINE video
          </label>
          <Controller
            name="basic_url"
            control={control}
            rules={{ required: true }}
            defaultValue={state.basic_url}
            render={({ field }) => (
              <input
                className="w-full	border-b border-black py-2 focus:outline-none"
                {...field}
              />
            )}
          />
          {errors.basic_url && '請輸入30秒 LOGLINE video'}
        </div>

        <div className="mt-10">
          <label className="block" htmlFor="adv_url">
            90秒故事簡介 video
          </label>
          <Controller
            name="adv_url"
            control={control}
            rules={{ required: true }}
            defaultValue={state.adv_url}
            render={({ field }) => (
              <input
                className="w-full	border-b border-black py-2 focus:outline-none"
                {...field}
              />
            )}
          />
          {errors.adv_url && '請輸入90秒故事簡介 video'}
        </div>

        <div className="mt-10">
          <label className="block" htmlFor="full_url">
            180秒完整大綱 video
          </label>
          <Controller
            name="full_url"
            control={control}
            rules={{ required: true }}
            defaultValue={state.full_url}
            render={({ field }) => (
              <input
                className="w-full	border-b border-black py-2 focus:outline-none"
                {...field}
              />
            )}
          />
          {errors.full_url && '請輸入180秒完整大綱 video'}
        </div>

        {/* <label htmlFor="tags">
          Tags
        </label> */}
        {/* <Controller
        name="hashtags"
        control={control}
        defaultValue={state.hashtags}
        render={({ field }: any) => (
          <Autocomplete
            {...field}
            multiple
            freeSolo
            options={[]}
            renderTags={(value: string[][], getTagProps) =>
              value.map((option: string[], index: number) => (
                // eslint-disable-next-line react/jsx-key
                <Chip
                  {...getTagProps({ index })}
                  variant="outlined"
                  label={option}
                />
              ))
            }
            renderInput={(params) => (
              <input type="text" {...params} />
            )}
          />
        )}
      /> */}

        <div className="mt-10">
          <label
            className="block w-full cursor-pointer border border-black py-2 text-center"
            htmlFor="uploader"
          >
            上傳完整劇本
          </label>
          <input
            id="uploader"
            type="file"
            ref={fileInput}
            hidden
            accept=".pdf"
            onChange={() => {
              handleUpload()
            }}
          />
          {fileInput.current?.files?.item(0)?.name}
        </div>
      </section>
    </form>
  )
}

export default ScriptForm
