import { NavLink } from 'react-router-dom'
import { motion } from 'framer-motion'
import { RoutePaths, RouteNames } from '../../enums/routeEnum'
import useBasePath from '../../hooks/useBasePath'
import Logo from '../../logo.svg'

const publicNavLinks = ['SCRIPT_LIST', 'ABOUT', 'ACTIVITIES']
const postLoginNavLinks = ['PROFILE', 'SCRIPT_SUBMITTED', 'SCRIPT_NEW']

const getNavLinks = () => {
  const basePath = useBasePath()
  switch (basePath.replace(/^\//i, '')) {
    case RoutePaths.PROFILE:
    case RoutePaths.SCRIPT_SUBMITTED:
    case RoutePaths.SCRIPT_NEW:
    case RoutePaths.SCRIPT_EDIT:
      return postLoginNavLinks
    default:
      return publicNavLinks
  }
}

type RouteStrings = keyof typeof RouteNames

const Header = () => {
  const isWelcomeAnimationLoaded =
    sessionStorage.getItem('welcomeAnimation') === 'loaded'

  return (
    <header className="sticky top-0 z-[1] h-[104px] bg-white">
      <div className="flex flex-col justify-between">
        <div className="flex h-[103px] items-center justify-between pl-6 pr-8">
          <motion.div
            className="flex gap-4"
            initial={
              isWelcomeAnimationLoaded ? false : { opacity: 0, y: '100%' }
            }
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 1, duration: 1.25, ease: 'easeOut' }}
          >
            <h1>
              <NavLink to="/">
                <img src={Logo} alt="三場戲" />
              </NavLink>
            </h1>
            <figure className="h-6 w-px bg-black" />
            <h2>話我知你劇本有幾勁</h2>
          </motion.div>
          <nav>
            <ul className="flex gap-x-6">
              {getNavLinks().map((to, i) => (
                <li key={to}>
                  <NavLink
                    to={`/${RoutePaths[to as RouteStrings]}`}
                    className="group block"
                  >
                    {({ isActive }) => (
                      <motion.div
                        initial={
                          isWelcomeAnimationLoaded
                            ? false
                            : { opacity: 0, y: '100%' }
                        }
                        animate={{ opacity: 1, y: 0 }}
                        transition={{
                          duration: 0.5,
                          delay: 1.1 + 0.25 * (i + 1),
                          ease: 'easeOut',
                        }}
                      >
                        {RouteNames[to as RouteStrings]}
                        <figure
                          className={`relative left-0 bottom-0 h-0.5 w-full origin-left scale-x-0 bg-black transition-all group-hover:scale-x-100 ${
                            isActive ? 'scale-x-100' : 'scale-x-0'
                          }`}
                        />
                      </motion.div>
                    )}
                  </NavLink>
                </li>
              ))}
            </ul>
          </nav>
        </div>
        <motion.figure
          className="block h-px w-full origin-left bg-black"
          initial={isWelcomeAnimationLoaded ? false : { scaleX: 0 }}
          animate={{ scaleX: 1 }}
          transition={{ duration: 1.25, ease: 'easeInOut' }}
        />
      </div>
    </header>
  )
}

export default Header
