import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { Script } from '../../typings'
import ScriptForm from '../../components/form/Script'
import AppContext from '../../context/AppContext'
import AuthContext from '../../context/AuthContext'

const EditPage = () => {
  const { fetchScript } = useContext(AppContext)
  const { authUser } = useContext(AuthContext)
  const [script, setScript] = useState<null | Script>(null)
  const { scriptId } = useParams()

  useEffect(() => {
    if (scriptId === undefined) {
      setScript({
        usernames: [authUser?.username].filter((v) => v) as string[],
        hashtags: [],
        name: '',
        basic_logline: '',
        adv_logline: '',
        logline_file_id: '',
        basic_url: '',
        adv_url: '',
        full_url: '',
        is_deleted: 0,
      })
    } else {
      fetchScript(scriptId ?? '').then((v) => {
        setScript(v)
      })
    }
  }, [authUser?.username, fetchScript, scriptId])

  if (script === null) {
    return <>Loading</>
  }

  return <ScriptForm script={script} />
}

export default EditPage
